@charset "UTF-8";
.my-virtual-select[data-v-4236ed3e] {
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  font-size: 13px;
  color: #555;
}
.my-virtual-select[data-v-4236ed3e] ::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 6px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 6px;
}
.my-virtual-select[data-v-4236ed3e] ::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    box-shadow: inset 0 0 5px rgba(97, 184, 179, 0.1);
    background: #dadada;
    border-radius: 6px;
}
.my-virtual-select[data-v-4236ed3e] ::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    box-shadow: none;
    border-radius: 6px;
    background: transparent;
}
.my-virtual-select .select-input-box[data-v-4236ed3e] {
    position: relative;
    background-color: white;
}
.my-virtual-select .select-input-box .input[data-v-4236ed3e] {
      flex: 1;
      width: 100%;
      padding: 0 32px 0 15px;
      height: 32px;
      line-height: 32px;
      border-radius: 4px;
      border: 1px solid #dcdfe6;
      outline: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      cursor: pointer;
}
.my-virtual-select .select-input-box .input[data-v-4236ed3e]::placeholder {
