}
.my-virtual-select .select-input-box .suffix-arrow[data-v-4236ed3e] {
      flex: none;
      width: 32px;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
      align-items: center;
      cursor: pointer;
}
.my-virtual-select .select-input-box .suffix-arrow[data-v-4236ed3e]::after {
        content: '';
        width: 8px;
        height: 8px;
        position: relative;
        top: -2px;
        border-right: 1px solid #c0c4cc;
        border-bottom: 1px solid #c0c4cc;
        background: white;
        transform: rotate(45deg);
        transition: all 300ms;
}
.my-virtual-select .select-input-box .suffix-arrow-focus[data-v-4236ed3e]::after {
      top: 2px;
      transform: rotate(-135deg);
}
.my-virtual-select .select-input-box .suffix-clear[data-v-4236ed3e] {
      flex: none;
      width: 32px;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      display: none;
      flex-flow: row nowrap;
      justify-content: center;
      align-items: center;
}
.my-virtual-select .select-input-box .suffix-clear .circle[data-v-4236ed3e] {
        position: relative;
        width: 13px;
        height: 13px;
        border: 1px solid #c0c4cc;
        border-radius: 100%;
        background: white;
}
.my-virtual-select .select-input-box .suffix-clear .circle[data-v-4236ed3e]::before {
          content: '';
          position: absolute;
          top: 2px;
          left: 5px;
          width: 1px;
          height: 8px;
          background-color: #c0c4cc;
          transform: rotate(-45deg);
}
.my-virtual-select .select-input-box .suffix-clear .circle[data-v-4236ed3e]::after {
          content: '';
          position: absolute;
          top: 2px;
          left: 5px;
          width: 1px;
          height: 8px;
          background-color: #c0c4cc;
          transform: rotate(45deg);
}
.my-virtual-select .select-input-box:hover .suffix-clear[data-v-4236ed3e] {
      display: flex;
}
.my-virtual-select .select-dropdown-box[data-v-4236ed3e] {
